  export default {
    name: 'Statistics',
    data() {
      var that = this;
      return {
        model: 'tab-2',
        train_time: 0,
        rank: 0,
        train_num: 0,
        accuracy: 0,
        practice_num: 0,
        completion_rate: 0,
        emailsSubscriptionChart: {
          data: {
            labels: ['Mon', 'Tue', 'Thu', 'Wen', 'Fri', 'Sat', 'Sun'],
            series: [
              [42, 43, 32, 80, 53, 43, 36],
            ],
          },
          options: {
            axisX: {
              showGrid: false,
            },
            low: 0,
            high: 100,
            chartPadding: {
              top: 0,
              right: 5,
              bottom: 0,
              left: 0,
            },
          },
          responsiveOptions: [
            ['screen and (max-width: 640px)', {
              seriesBarDistance: 5,
              axisX: {
                labelInterpolationFnc: function (value) {
                  return value[0]
                },
              },
            }],
          ],
        },
        headers: [{ //在这里相应的加变量即可
            sortable: false,
            text: '学号',
            value: 'student_number',
          },
          {
            sortable: false,
            text: '姓名',
            value: 'name',
          },
          {
            sortable: true,
            text: '学习时长',
            value: 'train_time',
            align: 'right',
          },
          {
            sortable: true,
            text: '题量',
            value: 'practice_num',
            align: 'right',
          },
          {
            sortable: true,
            text: '正确率',
            value: 'accuracy',
            align: 'middle',
          },
        ],
        accuracy_lastday: 0,
        accuracy_lastweek: 0,
        accuracy_lastmonth: 0,
        accuracy_lastyear: 0,
        train_time_lastday: 0,
        train_time_lastweek: 0,
        train_time_lastmonth: 0,
        train_time_lastyear: 0,
        trains_lastday: 0,
        trains_lastweek: 0,
        trains_lastmonth: 0,
        trains_lastyear: 0,
        train_questions_lastday: 0,
        train_questions_lastweek: 0,
        train_questions_lastmonth: 0,
        train_questions_lastyear: 0,
        checked_lastday: 0,
        checked_lastweek: 0,
        checked_lastmonth: 0,
        checked_lastyear: 0,
        week_done: 0,
        checked: 0,
        rank_list: [],
        flag13: false,
        items: [{
            student_number: 1,
            name: "李华",
            train_time: 30,
            practice_num: 1,
            accuracy: 22,
          },
          {
            student_number: 2,
            name: "张华",
            train_time: 30,
            practice_num: 30,
            accuracy: 75,
          },
          {
            student_number: 3,
            name: "小刘",
            train_time: 22,
            practice_num: 15,
            accuracy: 75,
          },
          {
            student_number: 4,
            name: "小润",
            train_time: 30,
            practice_num: 40,
            accuracy: 65,
          },
          {
            student_number: 5,
            name: "小泽",
            train_time: 23,
            practice_num: 30,
            accuracy: 50,
          },
        ],
        key: 0,
        times: ['本日', '最近一周', '最近一个月', '最近一年'],
        statistic_time: 'month',
        s_items: ['day', 'week', 'month', 'year'],
        flag100: 1,

      }
    },
    created() {
      var that = this;
      that.get_Info()
      setTimeout(() => {
        // console.log(that.rank_list[1].name);
        for (var i = 0; i < that.rank_list.length; i++)
        {
          if (that.statistic_time == 'month') {
            that.items[i].student_number = that.rank_list[i].student_number;
            that.items[i].name = that.rank_list[i].name;
            that.items[i].train_time = that.rank_list[i].train_time_lastmonth;
            that.items[i].practice_num = that.rank_list[i].train_questions_lastmonth;
            that.items[i].accuracy = that.rank_list[i].accuracy_lastmonth.toFixed(1).toString() + '%';
          }
          else if (that.statistic_time == 'week') {
            that.items[i].student_number = that.rank_list[i].student_number;
            that.items[i].name = that.rank_list[i].name;
            that.items[i].train_time = that.rank_list[i].train_time_lastweek;
            that.items[i].practice_num = that.rank_list[i].train_questions_lastweek;
            that.items[i].accuracy = that.rank_list[i].accuracy_lastweek.toFixed(1).toString() + '%';
          }
          else if (that.statistic_time == 'day') {
            that.items[i].student_number = that.rank_list[i].student_number;
            that.items[i].name = that.rank_list[i].name;
            that.items[i].train_time = that.rank_list[i].train_time_lastday;
            that.items[i].practice_num = that.rank_list[i].train_questions_lastday;
            that.items[i].accuracy = that.rank_list[i].accuracy_lastday.toFixed(1).toString() + '%';
          }
          else if (that.statistic_time == 'year') {
            that.items[i].student_number = that.rank_list[i].student_number;
            that.items[i].name = that.rank_list[i].name;
            that.items[i].train_time = that.rank_list[i].train_time_lastyear;
            that.items[i].practice_num = that.rank_list[i].train_questions_lastyear;
            that.items[i].accuracy = that.rank_list[i].accuracy_lastyear.toFixed(1).toString() + '%';
          }
        }
        if (that.flag100 == 1) {
          setTimeout(() => {
            that.flag13 = true
            that.$global.flag16 = true
            that.$forceUpdate();
          }, 6000)
        } else {
          alert("未加入班级！")
          that.flag13 = true
          that.$global.flag16 = true
          that.$forceUpdate();
        }
      }, 200)
    },
    computed: {
      // g_statistic_time() {
      //   return this.$global.statistic_time
      // }
    },
    methods: {
      get_Info: function () {
        var that = this;
        this.$axios({
          method: 'get',
          url: '/api/get_static_info/',
          params: {
            id: that.$global.userMsg.id,
          },
        })
        .then(function (response) {
          if (response.data.error_num == 0) {
            console.log(response.data)
            that.accuracy_lastday = response.data.accuracy_lastday
            that.accuracy_lastweek = response.data.accuracy_lastweek
            that.accuracy_lastmonth = response.data.accuracy_lastmonth
            that.accuracy_lastyear = response.data.accuracy_lastyear
            that.train_time_lastday = response.data.train_time_lastday
            that.train_time_lastweek = response.data.train_time_lastweek
            that.train_time_lastmonth = response.data.train_time_lastmonth
            that.train_time_lastyear = response.data.train_time_lastyear
            that.trains_lastday = response.data.trains_lastday
            that.trains_lastweek = response.data.trains_lastweek
            that.trains_lastmonth = response.data.trains_lastmonth
            that.trains_lastyear = response.data.trains_lastyear
            that.train_questions_lastday = response.data.train_questions_lastday
            that.train_questions_lastweek = response.data.train_questions_lastweek
            that.train_questions_lastmonth = response.data.train_questions_lastmonth
            that.train_questions_lastyear = response.data.train_questions_lastyear
            that.rank = response.data.rank
            that.emailsSubscriptionChart.data.series[0] = response.data.week_done
            that.rank_list = response.data.rank_list.reverse()
            if (that.statistic_time == 'month') {
              that.train_num = response.data.trains_lastmonth
              that.checked = response.data.checked_lastmonth.toFixed(1)
              that.accuracy = response.data.accuracy_lastmonth.toFixed(1)
              that.train_time = response.data.train_time_lastmonth
              that.practice_num = response.data.train_questions_lastmonth
            }
            else if (that.statistic_time == 'week') {
              that.train_num = response.data.trains_lastweek
              that.checked = response.data.checked_lastweek.toFixed(1)
              that.accuracy = response.data.accuracy_lastweek.toFixed(1)
              that.train_time = response.data.train_time_lastweek
              that.practice_num = response.data.train_questions_lastweek
            }
            else if (that.statistic_time == 'day') {
              that.train_num = response.data.trains_lastday
              that.checked = response.data.checked_lastday.toFixed(1)
              that.accuracy = response.data.accuracy_lastday.toFixed(1)
              that.train_time = response.data.train_time_lastday
              that.practice_num = response.data.train_questions_lastday
            }
            else if (that.statistic_time == 'year') {
              that.train_num = response.data.trains_lastyear
              that.checked = response.data.checked_lastyear.toFixed(1)
              that.accuracy = response.data.accuracy_lastyear.toFixed(1)
              that.train_time = response.data.train_time_lastyear
              that.practice_num = response.data.train_questions_lastyear
            }
          } else {
            that.flag100 = 0
          }
        })
      },
      change: function (index) {
        var that = this;
        this.statistic_time = this.s_items[index];
        console.log("statistic_time = ", this.statistic_time)
        if (that.statistic_time == 'month') {
          that.train_num = that.trains_lastmonth
          that.checked = that.checked_lastmonth
          that.accuracy = that.accuracy_lastmonth
          that.train_time = that.train_time_lastmonth
          that.practice_num = that.train_questions_lastmonth
        }
        else if (that.statistic_time == 'week') {
          that.train_num = that.trains_lastweek
          that.checked = that.checked_lastweek
          that.accuracy = that.accuracy_lastweek
          that.train_time = that.train_time_lastweek
          that.practice_num = that.train_questions_lastweek
        }
        else if (that.statistic_time == 'day') {
          that.train_num = that.trains_lastday
          that.checked = that.checked_lastday
          that.accuracy = that.accuracy_lastday
          that.train_time = that.train_time_lastday
          that.practice_num = that.train_questions_lastday
        }
        else if (that.statistic_time == 'year') {
          that.train_num = that.trains_lastyear
          that.checked = that.checked_lastyear
          that.accuracy = that.accuracy_lastyear
          that.train_time = that.train_time_lastyear
          that.practice_num = that.train_questions_lastyear
        }
        setTimeout(() => {
          for (var i = 0; i < that.rank_list.length; i++)
          {
            if (that.statistic_time == 'month') {
              that.items[i].student_number = that.rank_list[i].student_number;
              that.items[i].name = that.rank_list[i].name;
              that.items[i].train_time = that.rank_list[i].train_time_lastmonth;
              that.items[i].practice_num = that.rank_list[i].train_questions_lastmonth;
              that.items[i].accuracy = that.rank_list[i].accuracy_lastmonth.toFixed(1).toString() + '%';
            }
            else if (that.statistic_time == 'week') {
              that.items[i].student_number = that.rank_list[i].student_number;
              that.items[i].name = that.rank_list[i].name;
              that.items[i].train_time = that.rank_list[i].train_time_lastweek;
              that.items[i].practice_num = that.rank_list[i].train_questions_lastweek;
              that.items[i].accuracy = that.rank_list[i].accuracy_lastweek.toFixed(1).toString() + '%';
            }
            else if (that.statistic_time == 'day') {
              that.items[i].student_number = that.rank_list[i].student_number;
              that.items[i].name = that.rank_list[i].name;
              that.items[i].train_time = that.rank_list[i].train_time_lastday;
              that.items[i].practice_num = that.rank_list[i].train_questions_lastday;
              that.items[i].accuracy = that.rank_list[i].accuracy_lastday.toFixed(1).toString() + '%';
            }
            else if (that.statistic_time == 'year') {
              that.items[i].student_number = that.rank_list[i].student_number;
              that.items[i].name = that.rank_list[i].name;
              that.items[i].train_time = that.rank_list[i].train_time_lastyear;
              that.items[i].practice_num = that.rank_list[i].train_questions_lastyear;
              that.items[i].accuracy = that.rank_list[i].accuracy_lastyear.toFixed(1).toString() + '%';
            }
          }
          setTimeout(() => {
            that.$forceUpdate();
          }, 6000)
        }, 200)
        // that.key++
        // setTimeout(() => {
        //   that.key++
        // }, 800)
      },

    },
    watch: {
    
    }
  }
</script>